.Team_Members {
  //   background: #f4f6f9;
  //   box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.15);
  //   border-radius: 16px;
  height: auto;
  width: auto;
  .Header {
    margin-top: 40px;
    margin: 10px;
  }
  .Content {
    overflow: auto;
    height: calc(100vh - 100px);
    margin-left: 15px;
  }
  .showMoreText {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    /* identical to box height */

    text-align: center;
    text-decoration-line: underline;
    cursor: pointer;

    color: #24272c;

    opacity: 0.7;
  }
}
