.Container {
  width: 100%;
  overflow-y: auto;
  height: 100vh;
  .headContainer {
    padding: 10px;
    display: flex;
    align-items: center;
    margin-top: 6px;
    justify-content: space-between;
    .pageTitle {
      font-family: "Roboto";
      font-style: normal;
      font-weight: bold;
      font-size: 1vw;
      line-height: 14px;
      text-align: right;

      color: #24272c;
    }
  }
  .selectBusiness {
    margin-top: 7px;
    padding: 10px;
    .businessLabel {
      font-family: "Roboto";
      font-style: normal;
      font-weight: bold;
      font-size: 10px;
      line-height: 12px;

      color: #24272c;

      opacity: 0.5;
    }
  }
  .bottomButtons {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-left: -10px;
    bottom: 5px;
    width: 29%;
    .guideText {
      font-family: "Roboto";
      font-style: normal;
      font-weight: normal;
      font-size: 1vw;
      line-height: 40px;
      color: #24272c;
      mix-blend-mode: normal;
      opacity: 0.7;
      text-align: left;
    }
    .bottomButtom {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 500;
      font-size: 1.2vw;
      line-height: 18px;
      text-transform: none;
      margin: 10px;
      color: #ad0000;
    }
  }
  .listContainer {
    .listTextItem {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 500;
      font-size: 2vw;
      line-height: 18px;
      color: pink;
      opacity: 0.7;
    }
  }
}
a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}
@media (min-width: 1600px) {
  .Container {
    .headContainer {
      .pageTitle {
        font-size: 1.3vw;
      }
    }
    .listContainer {
      .listTextItem {
        font-size: 1.7rem;
      }
    }
  }
}
