.container {
  padding-left: 5%;
  padding-top: 1%;
  padding-bottom: 2%;
  padding-right: 5%;
  overflow: auto;
  height: 100%;

  .buttonDiv {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
  }
  .button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    height: 43px;
    margin: 8px;
  }
  .formTitle {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;

    color: #24272c;
  }
}

.disabledDay {
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  padding: 2px;
  margin: auto;
}
.activeDay {
  cursor: pointer;
  background: #12b0ee;
  height: 40px;
  padding: 2px;
  margin: auto;
  width: 40px;
  border-radius: 20px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.disabledDay:hover {
  background: rgb(158, 158, 158);
  height: 40px;
  width: 40px;
  border-radius: 20px;
  opacity: 0.4;
  text-transform: none;
}
